section {
    width: 100vw;
    padding: 30px 50px;
}

section div .back {
    text-decoration: none;
    width: 150px;
    height: 40px;
    background: #fff;
    color: #333;
    box-shadow: 0 0 5px #e2e2e2;
    border-radius: 5px;

    display: flex;
    justify-content: left;
    padding: 13px 50px;
}

/* above are back button styles */


/* below are country-info styles */

.country-info {
    display: grid;
    grid-template-columns: auto auto;
    
    padding: 40px;
}

.country-info img {
    width: 35vw;
    height: 55vh;
    box-shadow: 0 0 5px #e2e2e2;
    object-fit: cover;
}

.country-info .info {
    display: grid;
    grid-template-rows: auto auto auto;

    padding: 40px;

    align-content: left;
}

.country-info .info .country-details {
    display: grid;
    grid-template-columns: auto auto;

    margin-bottom: 50px;
}

.country-info .info .border-button {
    display: grid;
    /* grid-template-columns: auto auto; */
}

.country-info .info .border-button .btn {
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: center;
    align-items: center;

    width: 100px;
    height: 30px;
    /* padding: 7px; */

    color: #333;
    text-decoration: none;
    box-shadow: 0 0 5px #e2e2e2;
    border-radius: 5px;
}